@import "mixins";
// basic styles for form elements

.dark {
  button:not(.primary):not(.secondary) {
    background-color: rgba(255, 255, 255, 0.1) !important;
  }
  button:not(.primary) {
    color: rgba(255, 255, 255, 0.75) !important;
  }

  button.secondary:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }

  eo-form-input .description,
  .form-field .description {
    color: rgba(255, 255, 255, 0.8);
  }

  .form-field .ui-inputtext,
  input {
    color: var(--color-white);
  }
  .form-field.disabled {
    background-color: rgba(255, 255, 255, 0.1);
  }
  // inner button like datepicker
  .form-field button.ui-button {
    background-color: rgba(255, 255, 255, 0.2);
    color: var(--color-white);
  }
  // checkbox
  .ui-chkbox .ui-chkbox-box {
    background-color: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.4);
    color: rgba(255, 255, 255, 1);
  }

  .ui-inputtext {
    background-color: rgba(255, 255, 255, 0);
    color: var(--color-white);
  }

  // chips
  ul.ui-autocomplete-multiple-container li.ui-autocomplete-token,
  .ui-chips li.ui-chips-token {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.4);
    color: var(--color-white);

    .fa {
      font-size: 11px;
    }
  }
}

eo-form-input {

  yvc-chips-list {
    width: 100%;
  }
}
.form-element-range {
  // operator select in range form elements
  yvc-dropdown {
    background-color: var(--panel-divider-color);
    min-width: min-content;
    border: 0;
    cursor: pointer;
    yvc-icon {
      display: none;
    }
  }
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input::-ms-clear {
  display: none;
}

button.spinner {
  padding-right: 2em;
  position: relative;

  &::after {
    content: " ";
    position: absolute;
    margin: auto;
    top: calc(50% - 0.5em);
    right: 0.5em;
    width: 1em;
    height: 1em;
    display: inline-block;
    background: center center no-repeat;
    background-size: cover;
    animation: button-spin 1s infinite;
    box-sizing: border-box;
    border-width: 0.17em;
    border-color: currentColor currentColor currentColor transparent;

    border-radius: 50%;
    border-style: solid;
  }

  @keyframes button-spin {
    0% {
      transform: rotate(0);
      animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
      transform: rotate(180deg);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    to {
      transform: rotate(360deg);
    }
  }
}

button,
a.button {
  text-decoration: none;
  border: 1px solid transparent;
  background: rgba(0, 0, 0, 0.06);
  color: var(--text-color-caption);
  padding: calc(var(--app-pane-padding) / 2) var(--app-pane-padding);
  cursor: pointer;
  @include transition();

  &:not([disabled]):not(.active):hover {
    border: 1px solid var(--color-primary-3);
    color: var(--text-color-caption);
    background: rgba(0, 0, 0, 0.1);
  }

  &:not(.primary):focus {
    border-radius: 2px;
    border-color: var(--color-primary-3);
    box-shadow: inset 0 0px 0 1px var(--color-white);
  }

  &.primary {
    color: var(--color-white);
    background: var(--color-accent);

    &:not([disabled]):hover {
      background: var(--color-accent-darker);
      color: var(--color-white);
    }

    &:not([disabled]):focus {
      border-radius: 2px;
      box-shadow: inset 0 0px 0 1px var(--color-white);
    }

    &:active {
      transform: translateY(1px);
    }

    &:not([disabled]):hover {
      opacity: 0.8;
    }

    &.grey {
      background: var(--color-primary-3);

      &:not([disabled]):hover {
        background: var(--color-light-gray);
      }

      &:not([disabled]):focus {
        border-radius: 2px;
        border-color: var(--color-primary-3);
        box-shadow: inset 0 0px 0 1px var(--color-white);
      }

      &:active {
        transform: translateY(1px);
      }
    }

    &[disabled] {
      opacity: 0.5;
      cursor: default;
      // background: var(--color-primary-3);
    }
  }
  // button without background
  &.secondary {
    background-color: transparent;
    &:not([disabled]):hover {
      background: rgba(0, 0, 0, 0.06);
    }

    &:focus {
      border-radius: 2px;
      border-color: transparent;
      box-shadow: inset 0 0px 0 1px var(--color-primary-3);
    }
    &:active {
      transform: translateY(1px);
    }
  }

  &[disabled] {
    opacity: 0.5;
    cursor: default;
  }
}

.form-field {
  > * {
    flex: 1 1 auto;
  }
  .description {
    color: var(--text-color-caption);
  }
  &.disabled {
    .fe-wrapper {
      padding: calc(var(--app-pane-padding) / 8);
      border-radius: 2px;
      background-color: rgba(0, 0, 0, 0.06);
      border-color: rgba(0, 0, 0, 0.06) !important;
    }
  }

  .ui-state-disabled {
    opacity: 0.8;
  }
  .ui-widget:disabled {
    opacity: 1;
    resize: vertical;
  }

  button.ui-button {
    background: rgba(0, 0, 0, 0.08);
    border: 0;
    border-radius: 2px;
    color: var(--text-color-caption);
    cursor: pointer;
    width: 20px;
    height: 20px;

    &:enabled:active,
    &:enabled:focus {
      border: 0;
      background: var(--color-accent);
      color: var(--color-white);
    }
    &.ui-spinner-button:enabled:hover,
    &:enabled:active,
    &:enabled:hover {
      border: 0;
      background: var(--color-accent);
      color: var(--color-white);
    }
    .fa {
      color: currentColor !important;
    }

    eo-icon {
      width: 16px;
      height: 16px;
      margin: auto;
    }
  }

  input {
    padding: 0;
    border: 0 !important;
    background: transparent !important;
    color: var(--text-color-body);
    display: flex;
    align-items: center;
  }
}

// tokens
ul.chips > li.chip {
    background: rgba(0, 0, 0, 0.06);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    color: rgba(0, 0, 0, 0.54);
    margin: 2px 2px 0 0;
    display: flex;
    align-items: center; 
  }

